:root {
  --screen-width: 100vw;
  --speed: 500ms;
  --dark-grey: #707070;
  --light-grey: #777777;
  --footer-grey: rgba(157, 157, 157);
  --btn-background: rgba(0, 0, 0, 0.3);
  --primary-font: "Montserrat", sans-serif;
  --modal-font: "Open Sans", sans-serif;
}

body {
  /* padding: */
  /* env(safe-area-inset); */
  /* env(safe-area-inset-top, 20px) */
  /* env(safe-area-inset-right, 20px) */
  /* env(safe-area-inset-bottom, 20px) */
  /* env(safe-area-inset-left, 20px); */
  padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);
  overflow: hidden;
}

.App {
  position: relative;
  /* max-height: 100vh; */
  font-family: var(--primary-font);
  overflow: hidden;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
  color: white;
}

a {
  color: white;
  text-decoration: none;
}

.carousel-inner {
  /* border-radius: 0.6em 0 0 0.6em; */
  overflow: hidden !important;
  width: 100% !important;
  height: 100% !important;
}

.carousel-inner .carousel-item {
  float: none;
}

.carousel-indicators li {
  width: 5px !important;
  height: 5px !important;
  background-color: black !important;
  border-radius: 100%;
  padding: 0;
}

.carousel-control-next-icon {
  /* background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath fill='gray' d='M2.75 0l-1.5 1.5L3.75 4l-2.5 2.5L2.75 8l4-4-4-4z'/%3e%3c/svg%3e") !important; */
  background-image: url("./assets/images/right.png") !important;
  width: 25px !important;
  height: 25px !important;
}

.carousel-control-prev-icon {
  /* background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath fill='gray' d='M5.25 0l-4 4 4 4 1.5-1.5L4.25 4l2.5-2.5L5.25 0z'/%3e%3c/svg%3e") !important; */
  background-image: url("./assets/images/left.png") !important;
  width: 25px !important;
  height: 25px !important;
}

iframe {
  width: 100%;
  /* height: 100vh; */
}

.modal-backdrop {
  opacity: 0 !important;
}

.modal-dialog {
  height: calc(100% - 60px) !important;
  margin: 0 !important;
  /* margin-top: 87px !important; */
  max-width: 100% !important;
  height: 100%;
  padding: 0 !important;
}

.modal-content {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  flex-direction: row !important;
  background-color: rgba(255, 255, 255, 0.93) !important;
  max-width: 80%;
  max-height: 80%;
  border: none !important;
  border-radius: 0px !important;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

@media (max-width: 812px) {
  .modal {
    top: 41px;
    /* height: calc(100vh); */
  }

  .carousel-control-next-icon,
  .carousel-control-prev-icon {
    width: 20px !important;
    height: 20px !important;
  }
}
