.contactBtn {
  display: block;
  width: 100%;
  background-color: var(--btn-background);
  color: white;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.2rem;
  border: 1px solid gray;
  padding: 10px 20px;
  text-align: center;

  @media (max-width: 812px) {
    font-size: 0.5rem;
  }
}

.contactBtn:hover {
  text-decoration: none;
  color: white;
}