.sidebar {
  position: fixed;
  top: 50px;
  right: 0px;
  width: 250px;

  img {
    cursor: pointer;
    margin-left: auto;
    display: block;
    width: 30px;
  }
}

.toggleContainer {
  width: 40px;
  margin-left: auto;
  background-color: var(--btn-background);
  padding: 5px;
}

.showBurger {
  transform: rotate(45deg);
  transition-duration: 0.5s;
}

.hideBurger {
  transform: rotate(0deg);
  transition-duration: 0.5s;
}

.btnContainerHide {
  opacity: 0;
  transition: all 0.5s ease-out;
}

.btnContainerShow {
  opacity: 1;
  transition: all 0.5s ease-in;
}

.openSidebar {
  position: fixed;
  top: 15vh;
  right: 0px;
  z-index: 50;
  border-bottom-left-radius: 5px;
  transition: 1s;
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 100vh;
}

.closeSidebar {
  position: fixed;
  top: 15vh;
  right: -200px;
  z-index: 50;
  transition: 1s;
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 100vh;
}

@media (max-width: 812px) {
  .sidebar {
    top: 20px;
    width: 170px;

    img {
      width: 20px;
    }
  }

  .openSidebar, .closeSidebar {
    top: 17vh;
  }

  .toggleContainer {
    width: 30px;
  }

  .openSidebar {
    width: 130px;
  }
}
