.navDropdownBtn {
  // position: fixed;
  // top: 50px;
  // right: 50px;
  // width: 250px;
  // display: flex;
  // justify-content: space-between;
  text-align: center;
  color: white;
  background-color: var(--btn-background);
  font-family: var(--primary-font);
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.2rem;
  text-decoration: none;
  border: 1px solid gray;
  padding: 10px 20px;
  margin-bottom: 1rem;

  p {
    width: 100%;
    text-align: center;
    margin: 0;
  }

  i {
    margin-left: auto;
    cursor: pointer;
  }
}

.selectContainer {
  display: flex;
  justify-content: space-between; 
}

.countryDropdown {
  // position: fixed;
  // right: 50px;
  // top: 100px;
  // width: 186px;
  margin-top: 0.5rem;
  display: flex;
  flex-direction: column;
  font-size: 0.8rem;
  // border-radius: 10px;
  // padding: 1rem;
  // padding-top: 1rem;
  overflow: hidden;
  transition: height 1s ease;
  // border: 1px solid gray;
  // background-color: rgba(0, 0, 0, 0.3);
}

.dropdownItem {
  color: white;
  padding-bottom: 5px;
}

.dropdownItem:hover {
  color: white;
  filter: brightness(1.2);
  transform: scale(1);
}

@media (max-width: 812px) {
  .countryDropdown {
    font-size: 0.5rem;
  }
}

@media (max-width: 812px) {
  .navDropdownBtn {
    font-size: 0.5rem;
  }
}
