.modal {
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Overlay effect: translucent background: black w/ partial opacity */
  z-index: 9999; /* Overlay effect: positioned over other containers */
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  position: fixed; /* Fix position on the top-left corner*/
  top: 0;
  left: 0;
  overflow: auto; /* Enable scroll if needed */
  padding-top: 80px; /* Location of the content container */
}

.modalContent {
  max-width: 640px; /* Max width where it stops expanding */
  margin: auto; /* Auto margin according to the element width */
  padding: 10px;
}

.close {
  position: fixed;
  right: 2em;
  color: #e8e8e8;
  font-size: 1em;
  font-weight: bold;
  z-index: 99999;

  p {
    margin: 0;
  }

  @media (max-width: 812px) {
    font-size: 0.6em;
    right: 0.5em;
  }
}

.close:hover, 
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

.iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;
}