.footer {
  position: fixed;
  height: 50px;
  bottom: 15px;
  right: 0;
  text-align: right;
  margin-right: 50px;

  @media (max-width: 812px) {
    bottom: 0px;
    padding-top: 10px;
    margin-right: 60px;
  }
}

.footerLinks {
  margin: 0px 10px;
  padding: 15px;
  text-decoration: none;
  color: white;
  text-shadow: 2px 2px var(--dark-grey);
  font-weight: 700;
  font-family: 'Open Sans', sans-serif;

  @media (max-width: 812px) {
    font-size: 0.5em;
    padding: 7px;
    text-shadow: 1px 1px var(--dark-grey);
  }
}

.footerLinks:hover {
  text-decoration: none;
  color: white;
}