.container {
  display: flex;
  justify-content: space-between;
  width: 100%;

  @media (max-width: 812px) {
    overflow-y: scroll;
  }
}

.carouselColumn {
  // margin: auto;
  padding: 0;
  max-width: 60%;
  background-color: white;
  // border-radius: 0.6em;
  height: 100%;
}

.excerpt {
  font-size: 1em;
  margin-top: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  border-bottom: 2px solid var(--dark-grey);

  .excerptLeft {
    display: flex;
    align-items: center;
  }

  img {
    max-width: 55px;
    margin-right: 6px;
  }

  p {
    font-size: 1em;
    margin: 0;
    padding: 0.7em;
    width: 100%;
  }

  @media (max-width: 812px) {
    margin-top: 0;
    padding: 0.7em 0;
    border-width: 1px;
    font-size: 10px;

    img {
      max-width: 35px;
    }

    p {
      font-size: 0.8em;
      padding: 0;
    }
  }
}

.arText {
  text-align: center;
  font-size: 0.5em;
  margin-bottom: 3px;
}

.productDetails {
  font-family: var(--modal-font);
  font-weight: 400;
  border-radius: 0px 0px 10px 10px;
  max-width: 40%;
  padding: 1em;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: none;
}

.productDetails::-webkit-scrollbar {
  display: none;
}

.productContainer {
  overflow: auto;
  scrollbar-width: none;
}

.productDropdown {
  border: 1px solid black;
}

.productTitle {
  text-align: left;
  font-weight: 800;
  cursor: pointer;
  font-size: 0.95rem;
  padding: 1em;

  p {
    font-size: 1.5em;
  }

  @media (max-width: 812px) {
    padding: 0.7em;
    font-size: 8px;

    p {
      margin-bottom: 0.5em;
    }
  }
}

.indicationAndAR {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;

  img {
    cursor: pointer;
  }

  @media (max-width: 812px) {
    padding: 0.3em 0;
    font-size: 11px;

    img {
      max-height: 50px;
      margin-bottom: 6px;
    }
  }
}

.indication {
  list-style: none;
  font-size: 0.5em;
  letter-spacing: normal;
  color: var(--light-grey);
  padding: 0;
  display: block;
}

.productDescription {
  color: black;
  list-style-type: disc;
  font-size: 1em;
  padding-left: 1em;

  li {
    padding: 0;
    text-transform: none;
    font-weight: 400;
    letter-spacing: normal;
  }
}

.btnContainer {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: auto;
}

.btn {
  font-size: 0.6em;
  background-color: transparent;
  border: 1px solid var(--dark-grey);
  padding: 1em;
  margin-bottom: 10px;
  font-weight: 800;
  width: 45%;
  color: var(--light-grey);

  a {
    color: var(--light-grey);
    text-decoration: none;
  }

  a:hover {
    text-decoration: none;
    color: black;
  }

  @media (max-width: 812px) {
    font-size: 6px;
    padding: 0.7em 0.5em;
  }
}

.btn:hover {
  color: black;
}

.qrCode {
  border: 1px solid var(--dark-grey);
  margin-bottom: 10px;
}

@media (max-width: 812px) {
  .carouselColumn {
    max-width: 40%;
    margin: 0;
    display: flex;
    align-items: center;
  }

  .productDetails {
    max-width: 60%;
  }
}
